<template>
    <div class="modal-card">
        <header class="modal-card-head">
            <p class="modal-card-title">画像を編集</p>
        </header>
        <section class="modal-card-body">
            <croppa
                class="ca-croppa"
                v-model="croppa"
                :prevent-white-space="true"
                placeholder="画像を選択"
                :width="croppaSize"
                :height="croppaSize"
                :quality="464 / croppaSize"
                :show-remove-button="false"
            />
        </section>
        <footer class="modal-card-foot">
            <button class="button" @click="$parent.close()">
                キャンセル
            </button>
            <button class="button is-primary" @click="finish">
                決定
            </button>
        </footer>
    </div>
</template>

<script>
export default {
    name: "CroppaModal",
    props: {
        croppaSize: {
            type: Number,
            required: true,
            validator: v => v >= 0
        }
    },
    data: () => ({
        croppa: {}
    }),
    methods: {
        finish() {
            this.$emit("finish", this.croppa.generateDataUrl());
            this.$parent.close();
        }
    }
};
</script>

<style lang="scss" scoped>
.modal-card {
    width: 100%;
}
</style>
