<template>
    <div class="modal-card">
        <header class="modal-card-head">
            <p class="modal-card-title">画像を保存</p>
        </header>
        <section class="modal-card-body">
            <div
                class="ca-canvas-container"
                :style="{ height: `${canvasHeight}px` }"
            >
                <canvas
                    class="ca-canvas"
                    ref="canvas"
                    :width="width"
                    :height="height"
                ></canvas>
            </div>
            <div class="columns">
                <div class="column field">
                    <label class="label">幅</label>
                    <p class="control">
                        <input
                            class="input"
                            type="number"
                            v-model="width"
                            min="0"
                        />
                    </p>
                </div>
                <div class="column field">
                    <label class="label">高さ</label>
                    <p class="control">
                        <input
                            class="input"
                            type="number"
                            v-model="height"
                            min="0"
                        />
                    </p>
                </div>
            </div>
            <p>
                PCをお使いの方は画像を右クリックするとコピーでき、ツイート画面で
                Ctrl+V していただくと直接画像を貼り付けることができます。
            </p>
        </section>
        <footer class="modal-card-foot">
            <button class="button" @click="$parent.close()">
                キャンセル
            </button>
            <a
                class="button is-primary"
                :href="dataURL"
                :download="`${title1}.png`"
            >
                ダウンロード
            </a>
        </footer>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "SaveModal",
    mounted() {
        const { canvas } = this.$refs;
        this.ctx = canvas.getContext("2d");
        this.update();
        window.addEventListener("resize", this.resizeHandler);
        this.resizeHandler();
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.resizeHandler);
    },
    data: () => ({
        width: 960,
        ctx: null,
        timerId: 0,
        dataURL: "",
        canvasHeight: 0
    }),
    computed: {
        ...mapState(["title1"]),
        height: {
            get() {
                return Math.round(
                    (this.width / 480) * this.$store.getters.height
                );
            },
            set(val) {
                this.width = Math.round(
                    480 * (val / this.$store.getters.height)
                );
            }
        }
    },
    methods: {
        resizeHandler() {
            console.log("resize");
            this.canvasHeight =
                (this.$refs.canvas.getBoundingClientRect().width / this.width) *
                this.height;
        },
        update() {
            clearTimeout(this.timerId);
            this.timerId = setTimeout(async () => {
                const { ctx, width } = this,
                    { title1, title2, columns, images } = this.$store.state,
                    { height, imgSize } = this.$store.getters,
                    title2Arr = title2.split("\n"),
                    scale = width / 480,
                    fontFamily =
                        'BlinkMacSystemFont, -apple-system, "Roboto", "Noto Sans JP", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", "Meiryo", sans-serif';

                ctx.save();
                ctx.scale(scale, scale);

                ctx.fillStyle = "#FFFFFF";
                ctx.fillRect(0, 0, 480, height);

                ctx.fillStyle = "#4a90e2";
                ctx.fillRect(8, 8, 464, 150);

                const textMarginTop = (150 - 67.5 - 30 * title2Arr.length) / 2;
                ctx.fillStyle = "#FFFFFF";

                ctx.font = `bold 36px ${fontFamily}`;
                ctx.fillText(title1, 26, textMarginTop + 60, 428);

                ctx.font = `16px ${fontFamily}`;
                title2Arr.forEach((str, i) => {
                    ctx.fillText(
                        str,
                        26,
                        textMarginTop + 67.5 + 25 * (i + 1),
                        428
                    );
                });

                const allPromises = [];

                images.forEach(async (image, i) => {
                    const img = document.createElement("img"),
                        loadPromise = new Promise(resolve =>
                            img.addEventListener("load", resolve, {
                                once: true
                            })
                        );
                    allPromises.push(loadPromise);
                    img.src = image;
                    await loadPromise;
                    ctx.drawImage(
                        img,
                        8 + imgSize * (i % columns),
                        166 + Math.floor(i / columns) * imgSize,
                        imgSize - 8,
                        imgSize - 8
                    );
                });

                ctx.fillStyle = "#DFDFDF";
                ctx.fillRect(0, height - 70, 480, 2);

                ctx.fillStyle = "#737373";

                ctx.save();

                ctx.translate(18, height - 50);
                ctx.scale(4 / 3, 4 / 3);

                ctx.fill(
                    new Path2D(
                        `M 17.65 6.35
                        C 16.2 4.9 14.21 4 12 4
                        c -4.42 0 -7.99 3.58 -7.99 8
                        s 3.57 8 7.99 8
                        c 3.73 0 6.84 -2.55 7.73 -6
                        h -2.08
                        c -0.82 2.33 -3.04 4 -5.65 4 -3.31 0 -6 -2.69 -6 -6
                        s 2.69 -6 6 -6
                        c 1.66 0 3.14 0.69 4.22 1.78
                        L 13 11
                        h 7
                        V 4
                        l -2.35 2.35
                        z`
                    )
                );

                ctx.restore();
                ctx.save();

                ctx.translate(66, height - 50);
                ctx.scale(4 / 3, 4 / 3);

                ctx.fill(
                    new Path2D(
                        `M 12 1
                        c -4.97 0 -9 4.03 -9 9
                        v 7
                        c 0 1.66 1.34 3 3 3
                        h 3
                        v -8
                        H 5
                        v -2
                        c 0-3.87 3.13 -7 7 -7
                        s 7 3.13 7 7
                        v 2
                        h -4
                        v 8
                        h 3
                        c 1.66 0 3 -1.34 3 -3
                        v -7
                        c 0 -4.97 -4.03 -9 -9 -9
                        z`
                    )
                );

                ctx.restore();
                ctx.save();

                ctx.translate(114, height - 50);
                ctx.scale(4 / 3, 4 / 3);

                ctx.fill(
                    new Path2D(
                        `M 11 7
                        h 2
                        v 2
                        h -2
                        z
                        m 0 4
                        h 2
                        v 6
                        h -2
                        z
                        m 1 -9
                        C 6.48 2 2 6.48 2 12
                        s 4.48 10 10 10 10 -4.48 10 -10
                        S 17.52 2 12 2
                        z
                        m 0 18
                        c -4.41 0 -8 -3.59 -8 -8
                        s 3.59 -8 8 -8 8 3.59 8 8 -3.59 8 -8 8
                        z`
                    )
                );

                ctx.restore();

                ctx.fillStyle = "#4a90e2";
                ctx.beginPath();
                ctx.moveTo(360, height - 60);
                ctx.arcTo(470, height - 60, 470, height - 58, 2);
                ctx.arcTo(470, height - 12, 468, height - 12, 2);
                ctx.arcTo(358, height - 12, 358, height - 14, 2);
                ctx.arcTo(358, height - 60, 360, height - 60, 2);
                ctx.closePath();
                ctx.fill();

                ctx.fillStyle = "#FFFFFF";
                ctx.textAlign = "center";
                ctx.textBaseline = "middle";
                ctx.fillText("確認", 414, height - 36);

                await Promise.all(allPromises);

                this.dataURL = this.$refs.canvas.toDataURL("image/png");
            }, 200);
        },
        download() {}
    },
    watch: {
        width: "update",
        height: "update"
    }
};
</script>

<style lang="scss" scoped>
.ca-canvas-container {
    overflow: hidden;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;

    & .ca-canvas {
        width: 480px;
        max-width: 100%;
        border: 1px solid #e0e0e0;
    }
}
.ca-svg {
    display: none;
}
</style>
