<template>
    <div>
        <div class="field">
            <div class="control">
                <button
                    class="button is-primary is-fullwidth"
                    @click="saveImage"
                >
                    画像を保存
                </button>
            </div>
        </div>
        <div class="field">
            <label class="label">タイトル 1</label>
            <div class="control">
                <input
                    type="text"
                    class="input"
                    placeholder="お店の外観"
                    :value="title1"
                    @input="updateState(['title1', $event.target.value])"
                />
            </div>
        </div>
        <div class="field">
            <label class="label">タイトル 2</label>
            <div class="control">
                <textarea
                    class="textarea has-fixed-size"
                    placeholder="の画像をすべて選択してください。"
                    :value="title2"
                    @input="updateState(['title2', $event.target.value])"
                ></textarea>
            </div>
        </div>
        <div class="field">
            <label class="label">列の数</label>
            <div class="control">
                <input
                    type="number"
                    class="input"
                    :value="columns"
                    min="1"
                    step="1"
                    @input="
                        updateState(['columns', parseInt($event.target.value)])
                    "
                />
            </div>
        </div>
        <transition-group
            tag="div"
            name="image"
            class="field ca-images-container"
        >
            <div
                v-for="(img, i) in images"
                :key="img.slice(-40, -1)"
                class="ca-image-item"
            >
                <img class="ca-image-element" :src="img" />
                <button
                    class="ca-image-remove button is-danger"
                    @click="removeImage(i)"
                >
                    <span class="icon is-small">
                        <i class="fas fa-trash-alt"></i>
                    </span>
                </button>
                <div class="ca-image-controls buttons has-addons is-centered">
                    <button
                        class="button is-light"
                        :disabled="i === 0"
                        @click="moveImage({ from: i, to: i - 1 })"
                    >
                        <span class="icon is-small">
                            <i class="fas fa-arrow-left"></i>
                        </span>
                    </button>
                    <button
                        class="button is-light"
                        :disabled="i === images.length - 1"
                        @click="moveImage({ from: i, to: i + 1 })"
                    >
                        <span class="icon is-small">
                            <i class="fas fa-arrow-right"></i>
                        </span>
                    </button>
                </div>
            </div>
            <div class="control ca-image-item" key="addImage">
                <button class="button ca-add-image" @click="selectImage1">
                    画像を追加
                </button>
            </div>
        </transition-group>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

import SaveModal from "@/components/SaveModal.vue";
import CroppaModal from "@/components/CroppaModal.vue";

export default {
    name: "InputFields",
    mounted() {
        window.addEventListener("resize", this.resizeHandler);
        this.resizeHandler();
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.resizeHandler);
    },
    data: () => ({
        timerId: 0,
        croppaSize: 0
    }),
    computed: mapState(["title1", "title2", "columns", "images"]),
    methods: {
        ...mapMutations(["updateState", "removeImage", "moveImage"]),
        saveImage() {
            this.$modal.open({
                parent: this,
                component: SaveModal,
                hasModalCard: true
            });
        },
        selectImage1() {
            this.$modal.open({
                parent: this,
                component: CroppaModal,
                hasModalCard: true,
                props: {
                    croppaSize: this.croppaSize
                },
                width:
                    Math.min(
                        window.innerWidth - 100,
                        window.innerHeight - 300
                    ) + 40,
                events: {
                    finish: dataURL => {
                        this.$store.commit("addImage", { dataURL });
                    }
                }
            });
        },
        resizeHandler() {
            clearTimeout(this.timerId);
            this.timerId = setTimeout(() => {
                this.croppaSize = Math.max(
                    0,
                    Math.min(window.innerWidth - 100, window.innerHeight - 300)
                );
            }, 200);
        }
    }
};
</script>

<style lang="scss" scoped>
.ca-images-container {
    display: flex;
    flex-wrap: wrap;
    position: relative;

    & .ca-image-item {
        position: relative;
        width: 150px;
        padding: 5px;

        &.image-enter-active,
        &.image-leave-active,
        &.image-move {
            transition: 0.2s;
        }
        &.image-leave-active {
            position: absolute;
        }
        &.image-enter {
            opacity: 0;
        }
        &.image-leave-to {
            opacity: 0;
            transform: translate(100%, 100%);
        }

        & .ca-image-element {
            width: 100;
            height: auto;
        }
        & .ca-image-remove {
            position: absolute;
            top: 10px;
            right: 10px;
        }
        & .ca-image-controls {
            position: absolute;
            left: 10px;
            right: 10px;
            bottom: 10px;
            margin: auto;
        }

        & .ca-add-image {
            width: 100%;
            height: 100%;
        }
    }
}
</style>
