import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

const defaultState = {
    title1: "CAPgene",
    title2:
        "の画像をすべて選択してください。\nすべて選択し終わったら [確認] をクリックしてください。",
    columns: 4,
    images: []
};

export default new Vuex.Store({
    plugins: [createPersistedState()],
    state: defaultState,
    getters: {
        imgSize: state => 472 / state.columns,
        height: (state, getters) =>
            166 +
            Math.ceil(state.images.length / state.columns) * getters.imgSize +
            70
    },
    mutations: {
        updateState(state, [key, value]) {
            state[key] = value;
        },
        addImage(state, { dataURL }) {
            state.images.push(dataURL);
        },
        removeImage(state, index) {
            state.images.splice(index, 1);
        },
        moveImage(state, { from, to }) {
            state.images.splice(to, 0, state.images.splice(from, 1)[0]);
        }
    },
    actions: {}
});
